@import url(https://fonts.googleapis.com/css2?family=Courier+Prime&display=swap);
@font-face {
    font-family: Grotesk, sans-serif;
    src: url(/static/media/Grotesk.c13ab066.otf);
}

:root {
    --primary: #723636;
    --background: #F4E7DA;
    --dark: #232323;
    --light: #fafafa;
    --error: rgb(228, 46, 1);
    /* --spacing-one: calc(1rem * 0.5);
    --spacing-two: calc(1rem); */
}

* {
    box-sizing: border-box;
    font-family: 'Courier Prime', monospace;
}

/* ========================================================================================== */

html,
body {
    min-height: 100%;

}

body {
    margin: 0;
    padding: 0;
    font-weight: 400;
    line-height: 1.5;
    font-size: 16px;
    color: #232323;
    color: var(--dark);
}

.page-bg {
    background-color: #F4E7DA;
    background-color: var(--background);
    width: 100vw;
    height: 100vh;
}

/* ========================================================================================== */

.com-con {
    border-style: solid;
    border-color: #232323;
    border-color: var(--dark);
    padding: 10px; 
}
